.pagewrapper {
  height: 47px;
  text-align: center;
  margin-bottom: 5px;
}

.selectedPage {
  font-weight: 900;
  background-color: #eee;
}

.pagenumber {
  margin-right: 5px;
  border: 1px solid #eee;
  padding: 5px 10px;
  cursor: pointer;
}

.pagenumber:last-of-type {
  margin-right: 0;
}

.pagebtn {
  margin: 0 5px;
}