.users {
  display: flex;
  flex-direction: column ;
}

.user {
  display: flex;
  position: relative;
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 5px;
  margin-bottom: 10px;
}

.user:last-child {
  margin-bottom: 0;
}

.user__col:not(:last-child) {
  margin-right: 20px;
}

.user__photo {
  width: 100px;
  height: 100px;
  overflow: hidden;
  border-radius: 10px;
}

.user__fullname {
  text-align: center;
  font-size: 24px;
  margin-bottom: 5px;
}

.location {
  position: absolute;
  top: 10px;
  right: 10px;
}
